<template>
    <div class="m-jx3dat-plugins" v-loading="loading">
        <div class="m-plugins-header">
            <h1 class="m-plugins-title">
                <i :class="typeicon(subtype)"></i>{{ typemap[subtype] }}
            </h1>
        </div>
        <listbox
            :data="data"
            :total="total"
            :pages="pages"
            :per="per"
            :page="page"
            @appendPage="appendPage"
            @changePage="changePage"
        >
            <template slot="filter">
                <a
                    :href="publish_link"
                    class="u-publish el-button el-button--primary el-button--small"
                >
                    + 发布插件数据
                </a>
                <!-- 角标过滤 -->
                <markBy @filter="filter"></markBy>
                <!-- 排序过滤 -->
                <orderBy @filter="filter"></orderBy>
            </template>
            <!-- 搜索 -->
            <div class="m-jx3dat-search" slot="search-after">
                <el-input
                    class="m-jx3dat-input"
                    placeholder="请输入搜索内容"
                    v-model="search"
                    @change="loadPosts"
                >
                    <template slot="prepend">
                        关键词
                    </template>
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
            <!-- 列表 -->
            <div class="m-archive-list m-plugins-list" v-if="data.length">
                <ul class="u-list">
                    <li class="u-item" v-for="(item, i) in data" :key="i">
                        <!-- <a class="u-banner" :href="item.post.ID | postLink" :target="target">
                            <img
                                v-if="item.post.post_banner"
                                :src="showBanner(item.post.post_banner)"
                            />
                            <img
                                class="u-default-banner"
                                v-else
                                src="../assets/img/logo2.svg"
                            />
                        </a> -->

                        <h2 class="u-post" :class="{ isSticky: item.post.sticky }">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg>
                            <a
                                class="u-title"
                                :style="item.post.color | isHighlight"
                                :href="item.post.ID | postLink"
                                :target="target"
                                >{{ item.post.post_title }}</a
                            >
                            <span
                                class="u-marks"
                                v-if="item.post.mark && item.post.mark.length"
                            >
                                <i
                                    v-for="mark in item.post.mark"
                                    class="u-mark"
                                    :key="mark"
                                    >{{ mark | showMark }}</i
                                >
                            </span>
                        </h2>

                        <div class="u-desc">{{ item.post.post_content || item.post.post_title }}</div>

                        <div class="u-misc">
                            <img
                                class="u-author-avatar"
                                :src="item.author.avatar | showAvatar"
                                :alt="item.author.name"
                            />
                            <a
                                class="u-author-name"
                                :href="item.author.uid | authorLink"
                                target="_blank"
                                >{{ item.author.name }}</a
                            >
                            <span class="u-date">
                                Updated on
                                <time>{{
                                    item.post.post_modified | dateFormat
                                }}</time>
                            </span>
                        </div>
                    </li>
                </ul>
            </div>
        </listbox>
    </div>
</template>

<script>
import listbox from "@jx3box/jx3box-page/src/cms-list.vue";
import { getPosts } from "../service/post";
import {
    showAvatar,
    authorLink,
    showMinibanner,
    buildTarget,
    publishLink,
} from "@jx3box/jx3box-common/js/utils";
import dateFormat from "../utils/moment";
import { jx3dat_types } from "../assets/data/types.json";
const typeicons = {
    "2" : "el-icon-aim",
    "3" : "el-icon-news",
    "4" : "el-icon-brush",
    "5" : "el-icon-magic-stick"
}
import { cms as mark_map } from "@jx3box/jx3box-common/js/mark.json";

export default {
    name: "Plugins",
    props: [],
    data: function() {
        return {
            loading: false,

            data: [], //数据列表
            page: 1, //当前页数
            total: 1, //总条目数
            pages: 1, //总页数
            per: 10, //每页条目

            search: "",

            order: "", //排序
            mark: "", //角标
            
            typemap: jx3dat_types,
        };
    },
    computed: {
        params: function() {
            let params = {
                per: this.per,
                subtype: this.subtype,
            };
            if (this.search) {
                params.search = this.search;
            }
            if (this.order) {
                params.order = this.order;
            }
            if (this.mark) {
                params.mark = this.mark;
            }
            return params;
        },
        subtype: function() {
            return this.$store.state.subtype;
        },
        defaultBanner: function() {
            return this.subtype + ".png";
        },
        target: function (){
            return buildTarget()
        },
        publish_link: function(val) {
            return publishLink("jx3dat");
        },
    },
    watch: {
        subtype(newdata) {
            if (newdata != "1") this.changePage(1);
        },
    },
    methods: {
        loadPosts: function(i = 1, append = false) {
            let query = Object.assign(this.params, {
                page: i,
            });
            this.loading = true;
            getPosts(query, this)
                .then((res) => {
                    if (append) {
                        this.data = this.data.concat(res.data.data.list);
                    } else {
                        window.scrollTo(0, 0);
                        this.data = res.data.data.list;
                    }
                    this.total = res.data.data.total;
                    this.pages = res.data.data.pages;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        appendPage: function(i) {
            this.loadPosts(i, true);
        },
        changePage: function(i) {
            this.loadPosts(i);
        },
        filter : function (o){
            this[o['type']] = o['val']
            this.loadPosts();
        },
        showBanner: function(val) {
            return showMinibanner(val);
        },
        randomColor: function(i) {
            const colormap = [
                "rgb(143,179,204)",
                "rgb(151,204,172)",
                "rgb(151,204,172)",
                "rgb(204,167,151)",
                "rgb(204,167,151)",
                "rgb(155,151,204)",
                '#b7b3e4',
                '#a3c9e0',
                '#b4e5c9',
                '#dfe0ac',
                '#e1bfae',
                '#e8bce9'
            ];
            return colormap[i];
        },
        typeicon : function (subtype){
            return typeicons[subtype]
        }
    },
    filters: {
        authorLink: function(val) {
            return authorLink(val);
        },
        showAvatar: function(val) {
            return showAvatar(val);
        },
        dateFormat: function(val) {
            return dateFormat(val);
        },
        postLink: function(val) {
            return "./?pid=" + val;
        },
        isHighlight: function(val) {
            return val ? `color:${val};font-weight:600;` : "";
        },
        showMark: function(val) {
            return mark_map[val];
        },
    },
    mounted: function() {
        if (this.subtype != "1") this.loadPosts(1);
    },
    components : {
        listbox
    }
};
</script>

<style lang="less">
@import "../assets/css/plugins.less";
</style>
